@import '../../utility/utility.styles.scss';
// Alerts
.alert {
  width: 100%;
  padding: 0.8rem;
  opacity: 0.9;
  background: $background;
  color: #333;
  text-align: center;
  &.alert-primary {
    background-color: $shade-1;
  }
  &.alert-dark {
    background-color: $primary;
  }
  &.alert-success {
    background-color: $accent;
  }
  &.alert-danger {
    background-color: red;
  }
}
